import axios from "axios";
import appConfig from "../common/config.json";
import moment from "moment";

export function getTransferHistory(dateStr) {
  const url = appConfig.ROOT_PATH + "/private/get-transfer-history";
  const [from, to] = getDateRange(dateStr);

  return axios.post(url, {
    startDate: from,
    endDate: to,
    pageSize: 1000,
    offset: 0,
  });
}

