function getDateRange(dateStr) {
  // console.log(dateStr);

  let date = moment(dateStr, "DD-MMM-YYYY");
  if (!date.isValid()) {
    date = moment();
  }
  let from = new Date(date.year(), date.month(), date.date(), 0, 0, 0);
  let to = new Date(date.year(), date.month(), date.date(), 23, 59, 59);

  return [from.getTime(), to.getTime()];
}
