import React, { useEffect, useState } from "react";
import appConfig from "../../common/config.json";
import { getStores } from "../store/StoresAPI";
import ProductCell from "../ProductCell";
import { Link } from "react-router-dom";
import Template from "../../common/Template";

function Products(props) {
  const [data, setData] = useState({
    items: [],
    store: "",
    catalog: "",
  });

  useEffect(() => {
    let isCancel = false;
    const storeId = props.match.params.store;
    const catalogId = props.match.params.catalog;

    const _expiry = localStorage.getItem(appConfig.KEY_CACHE_EXPIRY);

    let _ary = localStorage.getItem(appConfig.KEY_CACHE_PRODUCTS);

    if (!_ary || Number(_expiry) <= new Date().getTime()) {
      getStores()
        .then((_) => {
          if (!isCancel) {
            parseData(
              localStorage.getItem(appConfig.KEY_CACHE_PRODUCTS),
              storeId,
              catalogId
            );
          }
        })
        .catch((error) => {
          console.log(error.response.data);
        });
    } else {
      if (!isCancel) {
        parseData(_ary, storeId, catalogId);
      }
    }

    return () => {
      isCancel = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props.match.params.store, props.match.params.catalog]);

  function parseData(ary, storeId, catalogId) {
    try {
      let _stores = localStorage.getItem(appConfig.KEY_CACHE_STORES);
      let _catalogs = localStorage.getItem(appConfig.KEY_CACHE_CATALOGS);

      let store = JSON.parse(_stores).find((a) => a.id === storeId);
      let catalog = JSON.parse(_catalogs).find((a) => a.id === catalogId);

      if (!store && !catalog) {
        props.history.push("/app");
        return;
      }

      let _items = JSON.parse(ary);
      _items.sort((a, b) => a.orderIndex - b.orderIndex);
      setData({
        items: _items.filter((d) => d.parentId === catalogId),
        store: store.title,
        catalog: catalog.title,
      });
    } catch (error) {
      console.log(error.message);
      props.history.push("/app");
    }
  }

  return (
    <Template>
      <div className="row">
        <div className="col-md-12">
          <nav aria-label="breadcrumb">
            <ol className="breadcrumb bg-white shadow-cm">
              <li className="breadcrumb-item">
                <Link to="/app" className="card-link">
                  Home
                </Link>
              </li>
              <li className="breadcrumb-item">
                <Link
                  to={"/app/" + props.match.params.store}
                  className="card-link"
                >
                  {data.store}
                </Link>
              </li>
              <li className="breadcrumb-item active">{data.catalog}</li>
            </ol>
          </nav>
        </div>
      </div>

      <div className="col-md-12">
        <div className="row">
          {data.items.map((s, index) => {
            return (
              <div key={s.id} className="col-6 col-sm-6 col-md-6 mb-3">
                <ProductCell
                  id={s.id}
                  logo={s.photoUrl}
                  title={s.title}
                  index={index}
                  price={s.sellingPrice}
                />
              </div>
            );
          })}
        </div>
      </div>
    </Template>
  );
}

export default Products;
