import React, { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

function PasswordInput(props) {
  const [mode, setMode] = useState({ type: "password" });

  let wrapperClass = "form-control";
  if (props.error && props.error.length > 0) {
    wrapperClass += " is-invalid";
  }

  function handleToggle(event) {
    event.preventDefault();
    const current = mode.type;
    setMode({
      type: current === "text" ? "password" : "text",
    });
  }

  return (
    <div className="form-group">
      <label className="font-weight-bold">{props.label}</label>
      <div className="input-group">
        <input
          type={mode.type}
          className={wrapperClass}
          placeholder={props.placeholder}
          value={props.value}
          onChange={props.onChange}
          name={props.name}
        />
        <div className="input-group-append">
          <a href="!#" onClick={handleToggle} className="input-group-text">
            <FontAwesomeIcon
              style={{ width: "24px" }}
              icon={["fas", mode.type === "text" ? "eye" : "eye-slash"]}
            />
          </a>
        </div>
        {props.error && <div className="invalid-feedback">{props.error}</div>}
      </div>
    </div>
  );
}

export default PasswordInput;
