import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useEffect, useReducer } from "react";
import Template from "../common/Template";
import { getNotifications } from "./NotificationAPI";

function reducer(state, action) {
  switch (action.type) {
    case "loading":
      return {
        loading: true,
      };
    case "success":
      return {
        data: action.data,
        loading: false,
      };
    case "error":
      return {
        loading: false,
        error: action.error,
      };
    default:
      return state;
  }
}

function Notification(props) {
  const [state, dispatch] = useReducer(reducer, { data: [], loading: false });

  useEffect(() => {
    let isCancel = false;
    document.title = "VStore | Notifications";
    dispatch({ type: "loading" });
    getNotifications()
      .then((resp) => {
        if (!isCancel) {
          dispatch({
            type: "success",
            data: resp.data,
          });
        }
      })
      .catch((error) => {
        console.log(error);
        let msg = "Something went wrong.";
        if (error.response) {
          let data = error.response.data;
          msg = data.errorCode ? data.errorCode : data;
        }
        !isCancel && dispatch({ type: "error", error: msg });
      });
    return () => {
      isCancel = true;
    };
  }, []);

  function formatDate(millis) {
    let date = new Date(millis);
    let formatter = Intl.DateTimeFormat("en-US", {
      dateStyle: "medium",
      timeStyle: "short",
    });
    return formatter.format(date);
  }

  return (
    <Template>
      <div className="row">
        <div className="col-md-12">
          <div className="d-flex justify-content-between">
            <span className="h4 my-auto">Notifications</span>
            <button className="btn btn-sm btn-outline-primary ">
              Delete All
            </button>
          </div>
          <hr />
          {state.loading && (
            <div className="text-center py-4">
              <div className="spinner-border" role="status">
                <span className="sr-only">Loading...</span>
              </div>
            </div>
          )}
          {state.error && (
            <div
              className="alert alert-danger alert-dismissible fade show"
              role="alert"
            >
              {state.error}
              <button
                type="button"
                className="close"
                data-dismiss="alert"
                aria-label="Close"
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
          )}

          {state.data && !state.data.length && (
            <div className="text-center text-muted">No notification found.</div>
          )}

          {state.data &&
            state.data.map((n) => {
              let read =
                n.status === "READ" ? " text-muted" : " font-weight-bold";
              return (
                <a
                  href="!#"
                  className="text-decoration-none"
                  key={n.notificationId}
                >
                  <div className="card border-0 rounded-lg shadow-cm mb-3 col-md-12 list-group-item-action">
                    <div className="card-body px-0">
                      <div className="media">
                        <div
                          className="mr-3 bg-primary rounded-circle text-white"
                          style={{
                            width: "46px",
                            height: "46px",
                          }}
                        >
                          <div className="d-flex align-items-center justify-content-center h-100">
                            <FontAwesomeIcon
                              icon={["fas", "envelope"]}
                              size="lg"
                            />
                          </div>
                        </div>
                        <div className="media-body">
                          <label
                            className={
                              "my-0" +
                              (n.status === "READ"
                                ? " font-weight-medium"
                                : " font-weight-bold")
                            }
                          >
                            {n.title}
                          </label>
                          <div className={"mb-2 small" + read}>
                            {formatDate(n.notificationDate)}
                          </div>
                          <p className={"card-text mb-0" + read}>{n.body}</p>
                        </div>
                      </div>
                    </div>
                  </div>
                </a>
              );
            })}
        </div>
      </div>
    </Template>
  );
}

export default Notification;
