import React, { useEffect, useState } from "react";
import appConfig from "./config.json";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Link, useHistory } from "react-router-dom";
import { getUnreadCount } from "../profile/ProfileAPI";

function NavNotification() {
  const [unread, setUnread] = useState(0);

  useEffect(() => {
    let isCancel = false;
    const _unread = window.sessionStorage.getItem(appConfig.KEY_NOTIFICATION);
    if (!_unread) {
      getUnreadCount()
        .then((resp) => {
          sessionStorage.setItem(appConfig.KEY_NOTIFICATION, resp.data.count);
          !isCancel && setUnread(resp.data.count);
        })
        .catch((error) => {
          console.log(error.response.data);
        });
    } else if (!isNaN(parseInt(_unread))) {
      !isCancel && setUnread(parseInt(_unread));
    }

    return () => {
      isCancel = true;
    };
  }, []);

  return (
    <span className="notification-badge badge badge-danger">
      {unread ? unread : ""}
    </span>
  );
}

function AppBar(props) {
  const history = useHistory();

  function logout(event) {
    event.preventDefault();
    localStorage.clear();
    sessionStorage.clear();
    history.push("/login");
  }

  return (
    <nav
      className={
        "navbar sticky-top navbar-expand navbar-dark shadow-sm px-0 " +
        (props.colorClass ? props.colorClass : "bg-primary")
      }
    >
      <div className="container">
        <div className="container d-flex justify-content-between">
          <Link className="navbar-brand" to="/app">
            <span className="h4 logo-text">VStore</span>
          </Link>

          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarNav"
            aria-controls="navbarNav"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>

          <div className="collapse navbar-collapse" id="navbarNav">
            {localStorage.getItem(appConfig.KEY_TOKEN) && (
              <>
                <ul className="navbar-nav font-weight-bold">
                  <li
                    className={"nav-item" + (props.active === "home" ? "" : "")}
                  >
                    <Link className="nav-link" to="/">
                      Home
                    </Link>
                  </li>
                  <li className="nav-item d-none d-lg-block">
                    <Link className="nav-link" to="/epin-history">
                      E-Pin History
                    </Link>
                  </li>
                  <li className="nav-item d-none d-lg-block">
                    <Link className="nav-link" to="/topup-history">
                      Topup History
                    </Link>
                  </li>
                </ul>
                <ul className="navbar-nav ml-auto">
                  <li className="nav-item">
                    <Link to="/notifications" className="nav-link">
                      <FontAwesomeIcon icon={["fas", "bell"]} size="lg" />
                      <NavNotification />
                    </Link>
                  </li>
                  <li className="nav-item dropdown">
                    <Link to="/profile" className="nav-link d-block d-lg-none">
                      <FontAwesomeIcon
                        icon={["fas", "user-circle"]}
                        size="lg"
                      />
                    </Link>
                    <a
                      href="!#"
                      className="nav-link dropdown-toggle d-none d-lg-block"
                      id="navbarDropdown"
                      role="button"
                      data-toggle="dropdown"
                    >
                      <FontAwesomeIcon
                        icon={["fas", "user-circle"]}
                        size="lg"
                      />
                    </a>
                    <div
                      className="dropdown-menu dropdown-menu-right"
                      aria-labelledby="navbarDropdown"
                    >
                      <Link className="dropdown-item" to="/">
                        Change Name
                      </Link>
                      <Link className="dropdown-item" to="/">
                        Change Password
                      </Link>
                      <div className="dropdown-divider"></div>
                      <a className="dropdown-item" href="!#" onClick={logout}>
                        Logout
                      </a>
                    </div>
                  </li>
                </ul>
              </>
            )}
          </div>
        </div>
      </div>
    </nav>
  );
}

export default AppBar;
