import { useEffect, useState } from "react";
import Template from "../common/Template";
import { getEpinHistory } from "./HistoryAPI";
import { useArrayFetch } from "../common/DataFetchHook";
import moment from "moment";
import $ from "jquery";
import Loading from "../common/Loading";
import ErrorMessage from "../common/ErrorMessage";
import PurchaseCell from "./PurchaseCell";
import { Redirect } from "react-router-dom";

function PurchaseEPinHistory(props) {
  const fetch = (dateString) => getEpinHistory(dateString);
  const qDate = moment(props.match.params.date, "DD-MMM-YYYY");

  const [date, setDate] = useState(
    !qDate.isValid() ? moment().format("DD-MMM-YYYY") : props.match.params.date
  );
  const [data, loading, error] = useArrayFetch(date, fetch);

  useEffect(() => {
    document.title = "VStore | ePIN History";
    let datePicker = $("#datepicker").datepicker({
      uiLibrary: "bootstrap4",
      format: "dd-mmm-yyyy",
      maxDate: new Date(),
      change: handleChange,
    });
    return () => {
      datePicker && datePicker.destroy();
    };
  }, [props.match.params.date]);

  function handleChange({ target }) {
    setDate(target.value);
  }

  if (props.match.params.date && !qDate.isValid()) {
    return <Redirect to="/epin-history" />;
  }

  return (
    <Template>
      <div className="row">
        <div className="col-md-12">
          <h4>E-Pin History</h4>
          <hr />
          <form className="form-row mb-3">
            <div className="col-auto">
              <input
                id="datepicker"
                type="text"
                className="form-control bg-white"
                defaultValue={date}
                readOnly
              />
            </div>
          </form>

          <Loading show={loading} />
          <ErrorMessage show={error} message={error} />

          {!loading && !error && data && !data.length && (
            <div className="text-center text-muted">No data.</div>
          )}

          {!loading &&
            !error &&
            data &&
            data.map((h) => {
              return (
                <PurchaseCell
                  key={h.purchaseId}
                  data={h}
                  detailRoute={
                    "/epin-history/detail/" +
                    h.purchaseId +
                    "/" +
                    date +
                    "?logo=" +
                    h.productPhotoUrl
                  }
                />
              );
            })}
        </div>
      </div>
    </Template>
  );
}

export default PurchaseEPinHistory;
