import moment from "moment";
import { Link } from "react-router-dom";

function PurchaseCell(props) {
  const h = props.data;

  let statusColor = " text-muted";
  switch (h.purchaseStatus) {
    case "PROCESSING":
      statusColor = " text-warning";
      break;
    case "SUCCESS":
      statusColor = " text-success";
      break;
    case "FAIL":
      statusColor = " text-danger";
      break;
    default:
      break;
  }

  return (
    <div className="card border-0 rounded-lg shadow-cm mb-3 col-md-12">
      <div className="card-body px-0">
        <div className="media">
          <img src={h.productPhotoUrl} alt="" className="mr-3" width="68" />
          <div className="media-body">
            <span className="my-0 h5 font-weight-medium">{h.productTitle}</span>
            <div className="mb-2 small text-muted">
              {moment(h.txnDate).format("DD-MMM-yyyy hh:mm:ss A")}
            </div>
            <p className="text-muted small mb-2">{h.description}</p>

            <label className="my-0 small font-weight-medium">STATUS</label>
            <div className={"mb-2 small font-weight-medium" + statusColor}>
              {h.purchaseStatus}
            </div>

            <label className="my-0 small font-weight-medium">PAID AMOUNT</label>
            <div className="mb-2 text-muted small">
              {h.paidAmount.toLocaleString("en", {
                minimumFractionDigits: 2,
              })}
            </div>

            <label className="my-0 small font-weight-medium">PURCHASE ID</label>
            <div className="mb-2 text-muted small">
              {h.purchaseId.replace(/\B(?=(\d{4})+(?!\d))/g, "-")}
            </div>

            <label className="my-0 small font-weight-medium">REMARK</label>
            <div className="mb-2 text-muted small">{h.remark}</div>

            <div className="clearfix">
              <Link
                to={props.detailRoute}
                className="btn btn-sm btn-primary px-3 float-right"
              >
                Detail
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default PurchaseCell;
