import React from "react";
import { library } from "@fortawesome/fontawesome-svg-core";
import { fab } from "@fortawesome/free-brands-svg-icons";
import { fas } from "@fortawesome/free-solid-svg-icons";
import { far } from "@fortawesome/free-regular-svg-icons";
import Login from "./authentication/login/Login";
import { Redirect, Route, Switch } from "react-router-dom";
import PrivateRoute from "./common/PrivateRoute";
import NotFound from "./common/NotFound";
import Register from "./authentication/register/Register";
import ResetPassword from "./authentication/reset/ResetPassword";
import AuthRoute from "./common/AuthRoute";
import axios from "axios";
import appConfig from "./common/config.json";
import { ToastContainer } from "react-toastify";
import Catalogs from "./service/catalog/Catalogs";
import Products from "./service/product/Products";
import Notification from "./notification/Notification";
import VPTransferHistory from "./history/VPTransferHistory";
import VPBalanceHistory from "./history/VPBalanceHistory";
import PurchaseEPinHistory from "./history/PurchaseEPinHistory";
import PurchaseTopupHistory from "./history/PurchaseTopupHistory";
import PurchaseEPinDetail from "./history/detail/PurchaseEPinDetail";
import PurchaseTopupDetail from "./history/detail/PurchaseTopupDetail";
import UserProfile from "./profile/UserProfile";
import Product from "./service/Product";

library.add(fab, fas, far);

var JSONbig = require("json-bigint")({ storeAsString: true });

axios.defaults.transformResponse = (data) => JSONbig.parse(data);
axios.interceptors.request.use(
  function (config) {
    const headers = {};

    const token = localStorage.getItem(appConfig.KEY_TOKEN);
    const userId = localStorage.getItem(appConfig.KEY_USER_ID);

    if (token && userId) {
      headers["Authorization"] = token;
      headers["X-USER-ID"] = userId;
    }

    headers["Content-Type"] = "application/json";

    config.headers = headers;
    return config;
  },
  function (error) {
    return Promise.reject(error);
  }
);

axios.interceptors.response.use(
  function (response) {
    console.log(response);
    return response;
  },
  function (error) {
    return Promise.reject(error);
  }
);

function App() {
  return (
    <>
      <ToastContainer autoClose={3000} hideProgressBar />
      <Switch>
        <PrivateRoute path="/app/:store/:catalog" component={Products} />
        <PrivateRoute path="/app/:store" component={Catalogs} />
        <PrivateRoute path="/app" exact component={Product} />
        <PrivateRoute path="/notifications" exact component={Notification} />
        <PrivateRoute path="/profile" exact component={UserProfile} />
        <PrivateRoute
          path="/transfer-history"
          exact
          component={VPTransferHistory}
        />
        <PrivateRoute
          path="/balance-history"
          exact
          component={VPBalanceHistory}
        />
        <PrivateRoute
          path="/epin-history/:date"
          exact
          component={PurchaseEPinHistory}
        />
        <PrivateRoute
          path="/epin-history"
          exact
          component={PurchaseEPinHistory}
        />
        <PrivateRoute
          path="/topup-history/:date"
          exact
          component={PurchaseTopupHistory}
        />
        <PrivateRoute
          path="/topup-history"
          exact
          component={PurchaseTopupHistory}
        />
        <PrivateRoute
          path="/epin-history/detail/:purchaseId/:date"
          exact
          component={PurchaseEPinDetail}
        />
        <PrivateRoute
          path="/topup-history/detail/:purchaseId/:date"
          exact
          component={PurchaseTopupDetail}
        />
        <AuthRoute path="/login" component={Login} />
        <AuthRoute path="/sign-up" component={Register} />
        <AuthRoute path="/reset-password" component={ResetPassword} />
        <Redirect from="/" to="/app" exact />
        <Route component={NotFound} />
      </Switch>
    </>
  );
}

export default App;
