import React, { useEffect, useState } from "react";
import appConfig from "../../common/config.json";
import { getStores } from "../store/StoresAPI";
import GridCell from "../GridCell";
import { Link } from "react-router-dom";
import Template from "../../common/Template";

function Catalogs(props) {
  const [data, setData] = useState({
    items: [],
    store: "",
  });

  useEffect(() => {
    let isCancel = false;
    const storeId = props.match.params.store;
    const _expiry = localStorage.getItem(appConfig.KEY_CACHE_EXPIRY);

    let _ary = localStorage.getItem(appConfig.KEY_CACHE_CATALOGS);

    if (!_ary || Number(_expiry) <= new Date().getTime()) {
      getStores()
        .then((_) => {
          if (!isCancel) {
            parseData(
              localStorage.getItem(appConfig.KEY_CACHE_CATALOGS),
              storeId
            );
          }
        })
        .catch((error) => {
          console.log(error.response.data);
        });
    } else {
      if (!isCancel) {
        parseData(_ary, storeId);
      }
    }
    return () => {
      isCancel = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props.match.params.store]);

  function parseData(ary, storeId) {
    let _stores = localStorage.getItem(appConfig.KEY_CACHE_STORES);

    if (!_stores) {
      props.history.push("/");
      return;
    }

    let store = JSON.parse(_stores).find((a) => a.id === storeId);

    if (!store) {
      props.history.push("/");
      return;
    }
    let _items = JSON.parse(ary);
    _items.sort((a, b) => a.orderIndex - b.orderIndex);
    setData({
      items: _items.filter((d) => d.parentId === storeId),
      store: store.title,
    });
  }

  return (
    <Template>
      <div className="row">
        <div className="col-md-12">
          <nav aria-label="breadcrumb">
            <ol className="breadcrumb bg-white shadow-cm">
              <li className="breadcrumb-item">
                <Link to="/app" className="card-link">
                  Home
                </Link>
              </li>
              <li className="breadcrumb-item active">{data.store}</li>
            </ol>
          </nav>
        </div>
      </div>

      <div className="col-md-12">
        <div className="row">
          {data.items.map((s, index) => {
            return (
              <div key={s.id} className="col-6 col-sm-6 col-md-6 mb-3">
                <GridCell
                  id={s.id}
                  logo={s.photoUrl}
                  title={s.title}
                  index={index}
                  slug={props.match.params.store + "/" + s.id}
                />
              </div>
            );
          })}
        </div>
      </div>
    </Template>
  );
}

export default Catalogs;
