function ProductCell(props) {
  let wrapperClass = "w-100";
  if (props.index % 2 === 0) {
    wrapperClass += " mr-1";
  } else {
    wrapperClass += " ml-1";
  }
  return (
    <a href="!#" className="text-decoration-none" onClick={props.onClick}>
      <div className="row h-100">
        <div className={wrapperClass}>
          <div className="card border-0 rounded-lg shadow-cm list-group-item-action h-100">
            <div className="card-body">
              <div className="row">
                <div className="mx-auto">
                  <img
                    src={props.logo}
                    height="50"
                    alt="..."
                    style={{
                      maxWidth: "100px",
                    }}
                  />
                </div>
              </div>

              <div
                className="text-secondary text-center user-select-none mt-3"
                style={{
                  maxWidth: "100%",
                  wordWrap: "break-word",
                }}
              >
                {props.title}
              </div>

              <h5
                className="text-success font-weight-medium text-center user-select-none mt-2 mb-0"
                style={{
                  maxWidth: "100%",
                  wordWrap: "break-word",
                }}
              >
                {props.price} VP
              </h5>
            </div>
          </div>
        </div>
      </div>
    </a>
  );
}

export default ProductCell;
