import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useEffect, useState } from "react";
import { Link, useHistory } from "react-router-dom";
import appConfig from "../common/config.json";
import { getBalance, getProfile } from "./ProfileAPI";

function UserDetail() {
  const [profile, setProfile] = useState({
    name: "...",
    mobile: "...",
  });

  useEffect(() => {
    let isCancel = false;
    const _profile = window.sessionStorage.getItem(appConfig.KEY_PROFILE);
    if (!_profile) {
      getProfile()
        .then((resp) => {
          const _data = { ...profile };
          _data.name = resp.data.name;
          _data.mobile = resp.data.mobile;
          window.sessionStorage.setItem(
            appConfig.KEY_PROFILE,
            JSON.stringify(_data)
          );
          !isCancel && setProfile(_data);
        })
        .catch((error) => {
          console.log(error.response.data);
        });
    } else {
      const _data = { ...profile };
      const parsed = JSON.parse(_profile);
      _data.name = parsed.name;
      _data.mobile = parsed.mobile;
      !isCancel && setProfile(_data);
    }
    return () => {
      isCancel = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <>
      <h4 className="text-center">{profile.name}</h4>
      <div className="text-center text-muted mb-3">{profile.mobile}</div>
    </>
  );
}

function MainBalance() {
  const [balance, setBalance] = useState(0.0);

  useEffect(() => {
    let isCancel = false;
    const _balance = window.sessionStorage.getItem(appConfig.KEY_BALANCE);
    if (!_balance) {
      getBalance()
        .then((resp) => {
          window.sessionStorage.setItem(
            appConfig.KEY_BALANCE,
            resp.data.balance
          );
          !isCancel && setBalance(parseFloat(resp.data.balance));
        })
        .catch((error) => {
          console.log(error.response.data);
        });
    } else if (!isNaN(parseFloat(_balance))) {
      !isCancel && setBalance(parseFloat(_balance));
    }
    return () => {
      isCancel = true;
    };
  }, []);

  return (
    <h5 className="text-center text-success mb-0">
      {balance.toLocaleString("en", {
        minimumFractionDigits: 2,
      })}{" "}
      VP
    </h5>
  );
}

