import { useEffect, useState } from "react";

export function useArrayFetch(date, dataSource) {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  useEffect(() => {
    let isCancel = false;
    setLoading(true);
    dataSource(date)
      .then((resp) => {
        if (!isCancel) {
          setLoading(false);
          setData(resp.data);
        }
      })
      .catch((error) => {
        let data = error.response.data;
        console.log(data);
        let msg = data.errorCode ? data.errorCode : data;
        if (!isCancel) {
          setLoading(false);
          setError(msg);
        }
      });
    return () => {
      isCancel = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [date]);

  return [data, loading, error];
}

export function useDataFetch(date, dataSource) {
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  useEffect(() => {
    let isCancel = false;
    setLoading(true);
    dataSource(date)
      .then((resp) => {
        if (!isCancel) {
          setLoading(false);
          setData(resp.data);
        }
      })
      .catch((error) => {
        let msg = "Something went wrong.";
        console.log(error);
        if (error.response) {
          let data = error.response.data;
          msg = data.errorCode ? data.errorCode : data;
        }
        if (!isCancel) {
          setLoading(false);
          setError(msg);
        }
      });
    return () => {
      isCancel = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [date]);

  return [data, loading, error];
}
