import { useEffect } from "react";
import { useDataFetch } from "../../common/DataFetchHook";
import ErrorMessage from "../../common/ErrorMessage";
import Loading from "../../common/Loading";
import Template from "../../common/Template";
import LabelGroup from "../../purchase/LabelGroup";
import PurchaseHeader from "../../purchase/PurchaseHeader";
import PurchaseStatus from "../../purchase/PurchaseStatus";
import { getTopupPurchase } from "../HistoryAPI";
import { Link, useLocation } from "react-router-dom";

function PurchaseTopupDetail(props) {
  const fetch = (id) => getTopupPurchase(id);
  const qDate = props.match.params.date;
  const [data, loading, error] = useDataFetch(
    props.match.params.purchaseId,
    fetch
  );

  const params = new URLSearchParams(useLocation().search);

  useEffect(() => {
    document.title = "VStore | Topup Purchase Detail";
  }, [props.match.params.purchaseId]);

  return (
    <Template>
      <div className="row">
        <div className="col-md-12">
          <nav aria-label="breadcrumb">
            <ol className="breadcrumb bg-white shadow-cm">
              <li className="breadcrumb-item">
                <Link to={"/topup-history/" + qDate} className="card-link">
                  Topup History
                </Link>
              </li>
              <li className="breadcrumb-item active">Topup Purchase Detail</li>
            </ol>
          </nav>
        </div>
      </div>
      <div className="row">
        <div className="col-md-12">
          <Loading show={loading} />
          <ErrorMessage show={error} message={error} />

          {!loading && !error && data && (
            <>
              <PurchaseHeader
                logo={params.get("logo")}
                title={data.productTitle}
                amount={data.paidAmount}
              />
              <PurchaseStatus status={data.status} />
              <div className="card border-0 rounded-lg shadow-cm mb-3 col-md-12">
                <div className="card-body">
                  <LabelGroup
                    label="PURCHASE ID"
                    // eslint-disable-next-line no-undef
                    value={data.purchaseId.replace(
                      /\B(?=(\d{4})+(?!\d))/g,
                      "-"
                    )}
                    marginBottom={true}
                  />
                  <LabelGroup
                    label="Recipient's Mobile"
                    value={data.recipientMobile}
                  />
                </div>
              </div>
            </>
          )}
        </div>
      </div>
    </Template>
  );
}

export default PurchaseTopupDetail;
