import axios from "axios";
import appConfig from "../../common/config.json";

export function getStores() {
  const url = appConfig.ROOT_PATH + "/private/load-stores";
  return axios.get(url).then((resp) => saveCache(resp.data));
}

function saveCache(data) {
  localStorage.setItem(appConfig.KEY_CACHE_EXPIRY, data.expiryDate);
  let stores = [];
  let catalogs = [];
  let products = [];
  for (let a in data.stores) {
    let store = data.stores[a];
    let s = { ...store };
    s["id"] = s.storeId;
    delete s.storeId;
    delete s.listingGroups;
    stores.push(s);
    for (let b in store.listingGroups) {
      let catalog = store.listingGroups[b];
      let c = { ...catalog };
      c["id"] = c.listingGroupId;
      c["parentId"] = store.storeId;
      delete c.listingGroupId;
      delete c.listings;
      catalogs.push(c);
      for (let c in catalog.listings) {
        let product = catalog.listings[c];
        let p = { ...product };
        p["id"] = p.listingId;
        p["parentId"] = catalog.listingGroupId;
        delete p.listingId;
        products.push(p);
      }
    }
  }
  localStorage.setItem(appConfig.KEY_CACHE_STORES, JSON.stringify(stores));
  localStorage.setItem(appConfig.KEY_CACHE_CATALOGS, JSON.stringify(catalogs));
  localStorage.setItem(appConfig.KEY_CACHE_PRODUCTS, JSON.stringify(products));
}
